body {
  margin: 0 auto;
  padding: 0;
}

.wrapper {
  margin-left: 70px;
  margin-right: 50px;
}


.navbar li {
    display: inline-block;
    text-align: center;
    font-family: 'Nunito', sans-serif;
    font-size: 20px;
  }
  
  .itemLeft {
    width: 20%;
  }
  
  .itemCenter {
      width: 50%;
  }
  
  .itemRight {
    width: 20%;
  }
.jumbotron {
	background-image: url(/static/media/kansas-city.b9c9fde4.png);
	background-repeat: round;
}

h1 {
	text-align: center;
    font-size: 50px;
	font-weight: bold;
	font-family: 'Calligraffitti', cursive;
}

h3 {
	text-align: center;
	font-size: 20px;
	padding-bottom: 40px;
	margin-left: 150px;
	margin-right: 150px;
	font-family: 'Nunito', sans-serif;
}
.card {
    width: 180px;
    height: 150px;
    margin: 15px 45px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
    float: left;
  }

  img {
    width: 180px;
    height: 150px;
  }


